import {AxiosResponse} from 'axios'
import {ThunkAction} from 'redux-thunk'
import {AppStateType} from './store'
import MainApi from '../api/main-api'

///////////// Const for actioncreators
const GET_ADS = 'GET_ADS'

type InitialStateMainPageDataType = {
    ads: Array<any> | null
}

///////////// Initial state
let initialState: InitialStateMainPageDataType = {
  ads: null
}

///////////// Reducer
const MainReducer = (state = initialState, action: ActionsTypes): InitialStateMainPageDataType => {
    switch (action.type) {
        case GET_ADS: {
            return {
                ...state,
                ads: action.ads
            }
        }
        default: {
            return state
        }
    }

}

///////////// Action creators
// actions types
type ActionsTypes = |
    GetAdsType

//
    
///////////// Thanks
// types for thunks
type ThunkActions = ThunkAction<Promise<void>, AppStateType, unknown, ActionsTypes>
//

type GetAdsType = {
    type: typeof GET_ADS
    ads: Array<any>
}

let GetAds = (ads: Array<any>): GetAdsType => ({type: GET_ADS, ads})


export let  GetAdsThunk = (): ThunkActions => async (dispatch) => {

    const response: AxiosResponse<any> = await MainApi.GetAds()
    
    if (response.status === 200) {
        dispatch(GetAds(response.data))
    }
}
/////////////

export default MainReducer