import axios from 'axios'
import {getCookie} from '../services/cookies_functions'

const instance = axios.create({ baseURL: 'https://60abde1c5a4de40017ccb188.mockapi.io/' })
// https://60abde1c5a4de40017ccb188.mockapi.io/
// http://localhost:2000/
// https://gebo-app.herokuapp.com/


const FreshToken = () => {
    return {
        headers: {
            'Authorization':  `Bearer ${getCookie('idUser')}`
        }
    }
}

const MainApi = {
    AddNewUser(userData: FormData) {
        return instance.post(`add-new-user`, userData )
    },
    GetAds() {
        return instance.get(`get-ads`, FreshToken())
    }
}

export default MainApi